@import url('https://fonts.googleapis.com/css?family=Lato');

* {
  box-sizing: border-box;
}


h1 {
  letter-spacing: 2px;
  text-align: center;
  text-transform: uppercase;
}

.countdown-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 3%;
}

.countdown-item {
  color: rgb(255, 255, 255);
  font-size: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  line-height: 30px;
  margin: 10px;
  padding-top: 10px;
  position: relative;
  width: 100px;
  height: 100px;
}

.countdown-item span {
  color: #ffffff;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
}

.countdown-svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100px;
  height: 100px;
}

.ant-modal-root {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;

}

.bg-send {
  background: url(../img/webp/bg1.png) no-repeat;
}

.element-form {
